/**
 * 异步组件库
 */
export default {
  namespaced: true,
  state: {
    asyncComponents: {
      'simple-grid': () =>
        import('@/views/simple-grid' /* webpackChunkName: "permission" */),
      'paging-grid': () =>
        import('@/views/paging-grid' /* webpackChunkName: "permission" */),
      'vue-iframe': () =>
        import('@/components/vue-iframe' /* webpackChunkName: "vue-iframe" */),
      'indicators-setting': () =>
        import('@/views/ticket-grant/indicators-setting' /* webpackChunkName: "ticket-grant" */),
      'taskrun-condition': () =>
        import('@/views/taskrun-monitor/taskrun-condition' /* webpackChunkName: "taskrun-condition" */),
      'taskrun-timeconsuming': () =>
        import('@/views/taskrun-monitor/taskrun-timeconsuming' /* webpackChunkName: "taskrun-timeconsuming" */),
      'taskrun-statistical-count': () =>
        import('@/views/taskrun-monitor/taskrun-statistical-count' /* webpackChunkName: "taskrun-statistic-count" */),
      'ticket-traditional-produce': () =>
        import('@/views/ticket-produce/ticket-traditional-produce' /* webpackChunkName: "taskrun-timeconsuming" */),
      'ticket-second-produce': () =>
        import('@/views/ticket-produce/ticket-second-produce' /* webpackChunkName: "taskrun-timeconsuming" */),
      // 硬件监控指标（合并之前的监控后台管理系统）
      'operation-maintenance-monitor': () =>
        import('@/views/hardware-monitor/operation-maintenance-monitor' /* webpackChunkName: "operation-maintenance-monitor" */),
      'port-connectivity-monitor': () =>
        import('@/views/hardware-monitor/port-connectivity-monitor' /* webpackChunkName: "port-connectivity-monitor" */),
      // 财政端 新增页面
      'tickets-maintain': () =>
        import('@/views/ticket-grant/tickets-maintain' /* webpackChunkName: "tickets-maintain" */),
      'ticket-produce-monitor': () =>
        import('@/views/ticket-produce/ticket-produce-monitor' /* webpackChunkName: "ticket-produce-monitor" */),
      'hosts-manage': () => import('@/views/hardware-monitor/hosts-manage'),
      // 权限管理
      'permission': () =>
        import('@/views/permission/index' /* webpackChunkName: "permission" */),
      'suspicious-bill-rule': () => import('@/views/hardware-monitor/suspicious-bill-rule')
    }
  }
}
